Template.settingsRKTasks.rendered = function () {
  var settingsRKTasks = {};
  Meteor.call('getSettingsRKTasks', "prefixMinutes", function (error, result) {
		if (!error) {
      if (typeof(Session.get("settingsRKTasks")) !== 'undefined') {
        settingsRKTasks = Session.get("settingsRKTasks");
      }
			settingsRKTasks.prefixMinutes = result;
      Session.set("settingsRKTasks", settingsRKTasks);
			return false;
		}
	});
  Meteor.call('getSettingsRKTasks', "folderPathForMinutesPrint", function (error, result) {
		if (!error) {
      if (typeof(Session.get("settingsRKTasks")) !== 'undefined') {
        settingsRKTasks = Session.get("settingsRKTasks");
      }
			settingsRKTasks.folderPathForMinutesPrint = result;
      Session.set("settingsRKTasks", settingsRKTasks);
			return false;
		}
	});
};

Template.settingsRKTasks.helpers({
  prefixMinutes: function () {
		var settings = Session.get("settingsRKTasks");
    if (typeof(settings) !== 'undefined') {
		    return settings.prefixMinutes;
    }
	},
  folderPathForMinutesPrint: function () {
   var settings = Session.get("settingsRKTasks");
   if (typeof(settings) !== 'undefined') {
       return settings.folderPathForMinutesPrint;
   }
 },
});

Template.settingsRKTasks.events({
  'submit form': function (e) {
    var data = {};
    e.preventDefault();
    data.prefixMinutes = e.target.prefixMinutes.value;
    data.folderPathForMinutesPrint = e.target.folderPathForMinutesPrint.value;
    Meteor.call('updateSettingsRKTasks', data, function (err) {
      if (!err) {
        if (typeof(toastr) !== 'undefined') {
          toastr.success(TAPi18n.__('Saved'));
        }
      }
    });
  },
});
