Template.printTask.rendered = function () {
	RKCore.log(this.data);
	document.title = "Task-" + this.data.currentTask._id;

	if (typeof($('#tags').val()) !== 'undefined') {
		$('#tags').val($('#tags').val().replace(/;/g, ','));
	}
	$('#tags').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	$('#projects').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});
};

Template.printTask.helpers({
	listActionee: function () {
		return this.data.usernames;
	},
	companyLogo: function () {
		if (Meteor.settings.public.companyLogo) {
			return Meteor.settings.public.companyLogo;
	  }
		return "/company/img/companylogo.png";
	},
	taskContent: function () {
		return new Spacebars.SafeString(this.currentTask.taskContent);
	},
	additionalText: function () {
		return new Spacebars.SafeString(this.currentTask.additionalText);
	},
	realized: function () {
		var value = this.currentTask.realized;
		if (value === "Yes") {
			val = TAPi18n.__("Yes");
		}
		else if (value === "No") {
			val = TAPi18n.__("No");
		}
		else {
			val = TAPi18n.__("On condition");
		}
		return val;
	},
	actionee: function () {
		var i;
		var val = '';
		var n;
		var actioneeId;
		var value = this.currentTask.actioneeIds;
		if (value) {
			n = value.length;
			for (i = 0; i < n; i++) {
				actioneeId = value[i];
				member = Members.collection.findOne(actioneeId);
				if (typeof(member) !== 'undefined') {
					val = val + "<span data-filterby='actionee' data-filterval='" + actioneeId + "' class='filterOnThis label label-info'>" + member.profile.nickname + "</span> ";
				}
			}
		}

		if (this.currentTask.actionee) {
			val2 = this.currentTask.actionee.replace(/;/g, ',');
			actionee = val2.split(',');
			n = actionee.length;
			for (i = 0; i < n; i++) {
				actioneeId = actionee[i];
				val = val + "<span class='label label-info'>" + actionee[i] + "</span> ";
				}
			val = val.trim();
			}
		return new Spacebars.SafeString(val);
	},
	projects: function () {
		var i;
		var val = '';
		var val2 = '';
		var value = this.currentTask.projectsIds;
		var nProjects = value.length;
		var projectId;
		RKCore.log("this.currentTask.projectsIds : ");
		RKCore.log(value);
		for (i = 0; i < nProjects; i++) {
			projectId = value[i];
			project = Projects.findOne(projectId);
			if (typeof(project) !== 'undefined') {
				val = val + "<span data-filterby='project' data-filterval='" + projectId + "' class='filterOnThis label label-info'>" + project.projectNumber + "</span> ";
			}
		}
		value = this.currentTask.otherProjects;
		if (value) {
			val2 = this.currentTask.otherProjects.replace(/;/g, ',');
			valSplitted = val2.split(',');
			htmlTag = valSplitted.map(function (tag) {
				return "<span class='label label-info'>" + tag + "</span>";
			});
			val = val + htmlTag.join(' ');
		}
		return new Spacebars.SafeString(val.trim());
	},
	tags: function () {
		var val;
		var value = this.currentTask.tags;
		if (value) {
			val = value.replace(/;/g, ',');
			valSplitted = val.split(',');
			htmlTag = valSplitted.map(function (tag) {
				return "<span class='label label-info'>" + tag + "</span>";
			});
			return new Spacebars.SafeString(htmlTag.join(' '));
		}
		return "";
	},
	author: function () {
		return Members.collection.findOne({accountId: this.currentTask.addedBy}).profile.nickname;
	},
	tasksDetails: function () {
		var i;
		var nTasks;
		var tasksDetails = [];
	  tasks = Tasks.find({ minutesIds: this.currentMinutes._id }).fetch();
		nTasks = tasks.length;
		for (i = 0; i < nTasks; i++) {
	    RKCore.log(tasks[i]);
			if (tasks[i].additionalText) {
				tasksDetails.push(
					{
						taskContent: new Spacebars.SafeString(tasks[i].taskContent),
						additionalText: new Spacebars.SafeString(tasks[i].additionalText),
					}
				);
			}
		}
		return tasksDetails;
	},
	Tasks: function () {
	  return Tasks.find({}).fetch();
	},
	settingsTasks: function () {
    return {
			rowsPerPage: 100,
			showFilter: false,
			showColumnToggles: false,
			showNavigation: 'never',
      class: 'table table-condensed col-sm-12 tablePrint',
			rowClass: 'blockRowClass',
			fields: [
				{
					key: 'actioneeIds',
					label: TAPi18n.__("Actionee"),
					sortable: false,
					fn: function (value, object) {
            var i;
            var val = '';
            var n;
            var actioneeId;
            if (value) {
              n = value.length;
              for (i = 0; i < n; i++) {
                actioneeId = value[i];
                member = Members.collection.findOne(actioneeId);
                if (typeof(member) !== 'undefined') {
                  val = val + member.profile.nickname + ", ";
                }
              }
            }

						if (object.actionee) {
							val2 = object.actionee.replace(/;/g, ',');
			        actionee = val2.split(',');
              n = actionee.length;
              for (i = 0; i < n; i++) {
                actioneeId = actionee[i];
                val = val + actionee[i] + ", ";
                }
              val = val.trim();
            }
						val = val.slice(0, - 1);
						return new Spacebars.SafeString(val);
					},
				},
				{
					key: 'scheduledEndDate',
					label: TAPi18n.__("Scheduled end date"),
					sortable: false,
				},
				{
					key: 'taskContent',
					label: TAPi18n.__("Content"),
					sortable: false,
					fn: function (value) {
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'realized',
					label: TAPi18n.__("Realized"),
					sortable: false,
					fn: function (value) {
						if (value === "Yes") {
								return new Spacebars.SafeString('<span class="glyphicon glyphicon-check" aria-hidden="true"></span>');
						}
						if (value === "No") {
								return "-";
						}
						return new Spacebars.SafeString(value);
					},
				},
			],
		};
	},
});
