Template.taskInSearchResults.helpers({
	memberUsername: function (who) {
			return Members.collection.findOne({accountId: who}).profile.nickname;
	},
	taskContent: function () {
		return new Spacebars.SafeString(this.taskContent);
	},
	taskAuthor: function () {
		var nickname = Members.collection.findOne({accountId: this.addedBy}).profile.nickname;
		var nicknameHTML = "<span class='label label-info'>" + nickname + "</span>";
		return new Spacebars.SafeString(nicknameHTML);
	},
	editLink: function () {
		var editLink = '<a href="' +
			Router.routes.editTask.path({_id: this._id})
			+ '" title="' + TAPi18n.__('Edit') + '"'
			+ '>'
			+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
			'</a> ';
		return new Spacebars.SafeString(editLink);
	},
	searchTypeIsFullTextSearch: function () {
		return (Session.get('searchType') === 'fullTextSearch');
	},
	textScore: function () {
		//limit to 2 digits after comma :
		return Math.round(this.score * 100) / 100;
	},
});
