Template.editMinutes.rendered = function () {
	var columns = [
		{
			data: 'actionee',
		},
  	{data: 'scheduledEndDate'},
  	{data: 'taskContent'},
  ];

  var colHeadersText = [];

  var colHeaders = [];
  var arrayLength = columns.length;
  var i;

  var demoData = [
      {scheduledEndDate: "", taskContent: "", actionee: ""},
  		{scheduledEndDate: "", taskContent: "", actionee: ""},
  ];

  var $container = $("#myHandsonTableDiv");

  colHeadersText.scheduledEndDate = TAPi18n.__("Scheduled end date");
  colHeadersText.taskContent = TAPi18n.__("Task");
  colHeadersText.actionee = TAPi18n.__("Actionee");

  for (i = 0; i < arrayLength; i++) {
    colHeaders.push(colHeadersText[columns[i].data]);
  }

  $container.handsontable({
    data: demoData,
    colHeaders: colHeaders,
    columns: columns,
    rowHeaders: false,
    minSpareRows: 1,
		stretchH: "all",
		colWidths: [20, 20, 100],
    contextMenu: true,
  });

	RKCore.log(this.data);

	if (typeof($('#tags').val()) !== 'undefined') {
		$('#tags').val($('#tags').val().replace(/;/g, ','));
	}
	$('#tags').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	if (typeof($('#projects').val()) !== 'undefined') {
		$('#projects').val($('#projects').val().replace(/;/g, ','));
	}
	$('#projects').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	Meteor.typeahead.inject();

	CKEDITOR.replace('minutesContent');
};

Template.editMinutes.helpers({
	isProjectChecked: function (projectsIds, projectId) {
		if (projectsIds.indexOf(projectId) < 0) {
			return false;
		}
		return "checked";
	},
	prefixMinutes: function () {
		var prefixMinutes = SettingsRKTasks.findOne({name: "prefixMinutes"});
		if (typeof(prefixMinutes) !== 'undefined') {
			return prefixMinutes.value;
		}
		return "DOC";
	},
	listActionee: function () {
		//var usernames = ["aa", "bb"];
		return this.data.usernames;
	},
	author: function () {
		return Members.collection.findOne({accountId: this.currentMinutes.addedBy}).profile.nickname;
	},
	Projects: function () {
		var myProjects = [];
		myProjects = Projects.find({}).fetch(); //to do get only ProcessStep
    return myProjects;
  },
	Tasks: function () {
	  return Tasks.find({}).fetch(); // filtered in publication
	},
	settingsTasks: function () {
    return {
			rowsPerPage: 10,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			fields: [
				{
					key: 'actioneeIds',
					label: TAPi18n.__("Actionee"),
					fn: function (value, object) {
            var i;
            var val = '';
            var n;
            var actioneeId;
            if (value) {
              n = value.length;
              for (i = 0; i < n; i++) {
                actioneeId = value[i];
                member = Members.collection.findOne(actioneeId);
                if (typeof(member) !== 'undefined') {
                  val = val + "<span class='label label-info'>" + member.profile.nickname + "</span> ";
                }
              }
            }
						if (object.actionee) {
							val2 = object.actionee.replace(/;/g, ',');
			        actionee = val2.split(',');
              n = actionee.length;
              for (i = 0; i < n; i++) {
                actioneeId = actionee[i];
                val = val + "<span class='label label-info'>" + actionee[i] + "</span> ";
                }
              val = val.trim();
              }
						return new Spacebars.SafeString(val);
					},
				},
				{
					key: 'scheduledEndDate',
					label: TAPi18n.__("Scheduled end date"),
				},
				{
					key: 'taskContent',
					label: TAPi18n.__("Task"),
					fn: function (value) {
						//var val = '<p class="editable" data-type="textarea" data-placeholder="Enter text" data-emptytext="Click to enter text" data-rows="4">' + value + '</p>';
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var markAsDoneLink = ' <a href="#" title="' + TAPi18n.__('Mark as done') + '" class="markAsDone" '
							+ 'data-taskid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-ok" aria-hidden="true"></span>' +
							'</a> ';
						if (object.additionalText) {
							//additionalText = getTruncatedTaskAdditionalText("#", object.additionalText) + ' '; //todo : remove html
              additionalTextLink = '<a href="#"'
  							+ ' class="showTaskAdditionalText" title="' + TAPi18n.__('Show follow-up') + '"'
  							+ ' data-taskid="' +	object._id + '"'
  							+ '>[...]</a> ';
						}
						var editLink = '<a href="' +
							Router.routes.editTask.path({_id: object._id, filterBy: Router.current().params.filterBy, filterVal: Router.current().params.filterVal})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							//+ ' data-wheretocomeback="' +	Router.current().params + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						var duplicateLink = '<a href="#"'
							+ ' class="duplicateTask" title="' + TAPi18n.__('Duplicate') + '"'
							+ ' data-taskid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-duplicate" aria-hidden="true"></span>' +
							'</a> ';
						var deleteLink = ' <a href="#" title="' + TAPi18n.__('Delete') + '" class="deleteTask" '
							+ 'data-taskid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-trash" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(markAsDoneLink + additionalTextLink + editLink + duplicateLink + deleteLink);
					},
				},
			],
		};
	},
});

Template.editMinutes.events({
	'submit form': function (e) {
		var data = {};
		var projectsIds = [];
		var hotInstance = $("#myHandsonTableDiv").handsontable('getInstance');
	  e.preventDefault();
		data.minutesId = this.currentMinutes._id;
		//data.stepNumber = e.target.stepNumber.value;
		data.location = e.target.location.value;
		data.minutesDate = e.target.minutesDate.value;
		data.author = e.target.author.value;
		data.recipients = e.target.recipients.value;
		data.attendeesPresent = e.target.attendeesPresent.value;
		data.objective = e.target.objective.value;
		data.minutesName = e.target.minutesName.value;
		data.minutesTitle = e.target.minutesTitle.value;
		data.minutesContent = e.target.minutesContent.value;
		data.tags = e.target.tags.value;
		data.projects = e.target.projects.value;
		$("input:checkbox.projectName[type=checkbox]:checked").each(function () {
			projectsIds.push($(this).val());
		});
		data.projectsIds = projectsIds;

		data.minutesContentNoHTML = $("<div/>").html(e.target.minutesContent.value).text();
		data.projectsNoHTML = $("<div/>").html(e.target.projects.value).text();
		data.tagsNoHTML = $("<div/>").html(e.target.tags.value).text();
		data.dataTaskTable = hotInstance.getData();

		RKCore.log(data);
	  Meteor.call('updateMinutes', data, function (error) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success('The minutes has been updated successfully');
				}
				if (typeof Router.current().params.filterBy !== 'undefined') {
					Router.go("minutes", {
						filterBy: Router.current().params.filterBy,
						filterVal: Router.current().params.filterVal,
					});
				}
				else {
					Router.go("minutes");
				}
			}
		});
		return false;
	},
	"click a.generatePDF": function (e) {
		var data = {};
    e.preventDefault();
		data.url = Router.routes.printMinutes.url({_id: e.currentTarget.dataset.minutesid});
		data.fileName = this.currentMinutes.minutesName + '.pdf';
    Meteor.call('generatePDF', data, function (err, res) {
			if (!err) {
				if (typeof(toastr) !== 'undefined') {
					filePath = res;
					toastr.success(TAPi18n.__('The minutes has been saved to') + ' : ' + filePath);
				}
				return false;
			}
		});
		return false;
	},
	"click a.delete": function (e) {
    e.preventDefault();
		bootbox.confirm(TAPi18n.__("Are you sure you want to delete this minutes ?"), function (result) {
		 if (result) {
			 Meteor.call('deleteMinutes', this._id, function () {});
		 }
		});
		return false;
	},
	"click a.markAsDone": function (e) {
    e.preventDefault();
    Meteor.call('markAsDone', e.currentTarget.dataset.taskid);
		return false;
	},
	"click a.showTaskAdditionalText": function (e) {
    var taskId = e.currentTarget.dataset.taskid;
	  e.preventDefault();
    myTask = Tasks.findOne(taskId);
    bootbox.dialog({
      title: myTask.taskContent,
      message: myTask.additionalText,
    });
		return false;
	},
	"click a.duplicateTask": function (e) {
    e.preventDefault();
    Meteor.call('duplicateTask', e.currentTarget.dataset.taskid, function (error, result) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success(TAPi18n.__('The task has been duplicated successfully. Please edit the task and save.'));
				}
				newTaskId = result;
				Router.go("editTask", {_id: newTaskId});
			}
		});
		return false;
	},
	"click a.deleteTask": function (e) {
    e.preventDefault();
		bootbox.confirm(TAPi18n.__("Are you sure you want to delete this task ?"), function (result) {
		 if (result) {
			 Meteor.call('deleteTask', e.currentTarget.dataset.taskid);
		 }
		});
		return false;
	},
});
