Template.minutes.rendered = function () {

};

Template.minutes.helpers({
	currentUsername: function () {
		var currentUserId = Meteor.userId();
		var currentUser = Members.collection.findOne({accountId: currentUserId});
		var currentUsername = currentUser.profile.nickname;
		return currentUsername;
	},
	currentUserId: function () {
		return Meteor.userId();
	},
	Projects: function () {
		return Projects.find({}).fetch();
	},
	Actionees: function () {
		var allTasks;
		var j;
		var k;
    var actioneeListTemp = [];
		var actioneeList = [];
		var nTasks;
		var nActioneesInThisTask;
		var actioneesArray;
		var actioneeName;
		allTasks = Tasks.find({}).fetch(); //to do get only ProcessStep
    nTasks = allTasks.length;
    j = 0;
		for (i = 0; i < nTasks; i++) {
			actioneesArray = allTasks[i].actionee.split(",");
			nActioneesInThisTask = actioneesArray.length;
			for (k = 0; k < nActioneesInThisTask; k++) {
				actioneeName = actioneesArray[k].trim();
				if (actioneeName.length > 0) {
					if (actioneeListTemp.indexOf(actioneeName) < 0) {
		        actioneeListTemp[j] = actioneeName;
						actioneeList.push({"actioneeName": actioneeName});
		        j++;
		      }
				}
			}
		}
    return actioneeList;
	},
	Minutes: function () {
		return Minutes.find().fetch();
	},
	settingsMinutes: function () {
    return {
			rowsPerPage: 10,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			fields: [
				{
					key: 'addedDate',
					label: TAPi18n.__("Added date"),
				},
				{
					key: 'addedBy',
					label: TAPi18n.__("Author"),
					fn: function (value) {
						var nickname = Members.collection.findOne({accountId: value}).profile.nickname;
						var nicknameHTML = "<span class='label label-info'>" + nickname + "</span>";
						return new Spacebars.SafeString(nicknameHTML);
					},
				},
				{
					key: 'minutesName',
					label: TAPi18n.__("Minutes"),
				},
				{
					key: 'projectsIds',
					label: TAPi18n.__("Projects"),
					fn: function (value) {
						var i;
						var val = '';
						var nProjects = value.length;
						var projectId;
						for (i = 0; i < nProjects; i++) {
							projectId = value[i];
							project = Projects.findOne(projectId);
							if (typeof(project) !== 'undefined') {
								val = val + "<span class='label label-info'>" + project.projectNumber + "</span> ";
							}
						}
						return new Spacebars.SafeString(val.trim());
					},
				},
				{
					key: 'tags',
					label: TAPi18n.__("Tags"),
					fn: function (value) {
						var val = value.replace(/;/g, ',');
		        tag = val.split(',');
		        htmlTag = tag.map(function (tag) {
		          return "<span class='label label-info'>" + tag + "</span>";
		        });
		        return new Spacebars.SafeString(htmlTag.join(' '));
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var editLink = '<a href="' +
							Router.routes.editMinutes.path({_id: object._id, filterBy: Router.current().params.filterBy, filterVal: Router.current().params.filterVal})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						var printLink = '<a href="' +
							Router.routes.printMinutes.path({_id: object._id})
							+ '" title="' + TAPi18n.__('Print') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-print" aria-hidden="true"></span>' +
							'</a> ';
						var generatePDFLink = '<a href="#"'
							+ ' class="generatePDF" title="' + TAPi18n.__('Save as a pdf file to predefined server location') + '"'
							+ ' data-minutesid="' +	object._id + '"'
							+ ' data-minutesname="' +	object.minutesName + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-floppy-disk" aria-hidden="true"></span>' +
							'</a> ';
						var duplicateLink = '<a href="#"'
							+ ' class="duplicateMinutes" title="' + TAPi18n.__('Duplicate') + '"'
							+ ' data-minutesid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-duplicate" aria-hidden="true"></span>' +
							'</a> ';
						var deleteLink = ' <a href="#" title="' + TAPi18n.__('Delete') + '" class="deleteMinutes" '
							+ 'data-minutesid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-trash" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(editLink + printLink + generatePDFLink + duplicateLink + deleteLink);
					},
				},
			],
		};
	},
});

Template.minutes.events({
	"click a.createMinutes": function (e) {
			var data = {};
			e.preventDefault();
	    Meteor.call('createMinutes', data, function (error, result) {
				if (!error) {
					minutesId = result;
					Router.go("editMinutes", {_id: minutesId});
				}
			});
			return false;
	},
	"click a.generatePDF": function (e) {
		var data = {};
    e.preventDefault();
		data.url = Router.routes.printMinutes.url({_id: e.currentTarget.dataset.minutesid});
		data.fileName = e.currentTarget.dataset.minutesname + '.pdf';
    Meteor.call('generatePDF', data, function (err, res) {
			if (!err) {
				if (typeof(toastr) !== 'undefined') {
					filePath = res;
					toastr.success(TAPi18n.__('The minutes has been saved to') + ' : ' + filePath);
				}
				return false;
			}
		});
		return false;
	},
	"click a.deleteMinutes": function (e) {
	    e.preventDefault();
			bootbox.confirm(TAPi18n.__("Are you sure you want to delete this minutes ?"), function (result) {
 			 if (result) {
 				 Meteor.call('deleteMinutes', e.currentTarget.dataset.minutesid);
 			 }
 		 	});
			return false;
	},
	"click a.duplicateMinutes": function (e) {
	    e.preventDefault();
	    Meteor.call('duplicateMinutes', e.currentTarget.dataset.minutesid, function (error, result) {
				if (!error) {
					newId = result;
					Router.go("editMinutes", {_id: newId});
				}
			});
			return false;
	},
	"change #filterByProjectSelect": function (e) {
		e.preventDefault();
		if (e.target.value === "all") {
			Router.go("minutes");
		}
		else {
			Router.go("minutes", {filterBy: 'project', filterVal: e.target.value});
		}
		return false;
	},
});
