Template.editTask.rendered = function () {
	if (this.data.currentTask.realized === "Yes") {
		document.getElementById('inlineRadioYes').checked = "checked";
	}
	else if (this.data.currentTask.realized === "onCondition") {
		document.getElementById('inlineRadioOnCondition').checked = "checked";
	}
	else {
		document.getElementById('inlineRadioNo').checked = "checked";
		//$("#inlineRadioNo").prop("checked", true);
	}

	if (typeof($('#tags').val()) !== 'undefined') {
		$('#tags').val($('#tags').val().replace(/;/g, ','));
	}
	$('#tags').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	if (typeof($('#otherProjects').val()) !== 'undefined') {
		$('#tags').val($('#tags').val().replace(/;/g, ','));
	}
	$('#otherProjects').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	if (typeof($('#actionee').val()) !== 'undefined') {
		$('#actionee').val($('#actionee').val().replace(/;/g, ','));
	}
	$('#actionee').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	$('#effectiveStartDate').datepicker({format: 'dd.mm.yyyy'});
	$('#effectiveEndDate').datepicker({format: 'dd.mm.yyyy'});
	$('#scheduledEndDate').datepicker({format: 'dd.mm.yyyy'});
	$('#scheduledStartDate').datepicker({format: 'dd.mm.yyyy'});

	Meteor.typeahead.inject();

	CKEDITOR.replace('additionalText');
	CKEDITOR.replace('taskContent');
};

Template.editTask.helpers({
	today: function () {
		return moment().format("DD.MM.YYYY");
	},
	tomorrow: function () {
		var tomorrow = moment().businessAdd(1, 'days');
		return moment(tomorrow).format("DD.MM.YYYY");
	},
	isProjectChecked: function (projectsIds, projectId) {
		if (projectsIds.indexOf(projectId) < 0) {
			return false;
		}
		return "checked";
	},
	Projects: function () {
		var myProjects = [];
		myProjects = Projects.find({}).fetch(); //to do get only ProcessStep
    return myProjects;
  },
	isMinutesChecked: function (minutesIds, minutesId) {
		if (typeof(minutesIds) !== 'undefined') {
			if (minutesIds.indexOf(minutesId) < 0) {
				return false;
			}
			return "checked";
		}
		return false;
	},
	Minutes: function () {
		var myMinutes = [];
		//myMinutes = Minutes.find({ projectsIds: {$in: this.currentTask.projectsIds} }).fetch();
		myMinutes = Minutes.find({}).fetch(); //to do get only minutes in this projetc
    return myMinutes;
  },
	listActionee: function () {
		//var usernames = ["aa", "bb"];
		return this.data.usernames;
	},
	Members: function () {
		return Members.collection.find({}).fetch();
	},
	isActioneeChecked: function (actioneeIds, actioneeId) {
		if (typeof(actioneeIds) !== 'undefined') {
			if (actioneeIds.indexOf(actioneeId) < 0) {
				return false;
			}
			return "checked";
		}
		return false;
	},
	showInGantt: function () {
		if (this.currentTask.showInGantt) {
			return "checked";
		}
		return "";
	},
	sendReminder: function () {
		if (this.currentTask.sendReminder) {
			return "checked";
		}
		return "";
	},
	sendReminderActionee: function () {
		if (this.currentTask.sendReminderActionee) {
			return "checked";
		}
		return "";
	},
	author: function () {
		return Members.collection.findOne({accountId: this.currentTask.addedBy}).profile.nickname;
	},
	markedAsDoneBy: function () {
		var member = Members.collection.findOne({accountId: this.currentTask.markedAsDoneBy});
		if (typeof(member) !== 'undefined') {
			markedAsDoneBy = member.profile.nickname;
		}
		else {
			markedAsDoneBy = TAPi18n.__('The task is still open');
		}
		return markedAsDoneBy;
	},
});

Template.editTask.events({
	'submit form': function (e) {
		var data = {};
		var projectsIds = [];
		var minutesIds = [];
		var actioneeIds = [];
	  e.preventDefault();
		data.taskId = this.currentTask._id;
		data.effectiveStartDate = e.target.effectiveStartDate.value;
		data.effectiveEndDate = e.target.effectiveEndDate.value;
	  data.scheduledStartDate = e.target.scheduledStartDate.value;
		data.scheduledEndDate  = e.target.scheduledEndDate.value;
		data.showInGantt  = e.target.showInGantt.checked;
		data.sendReminder  = e.target.sendReminder.checked;
		data.sendReminderActionee  = e.target.sendReminderActionee.checked;
		data.taskContent = e.target.taskContent.value;
		data.otherProjects = e.target.otherProjects.value;
		data.tags = e.target.tags.value;

		if (e.target.inlineRadioYes.checked) {
			data.realized = e.target.inlineRadioYes.value;
		}
		else if (e.target.inlineRadioNo.checked) {
			data.realized = e.target.inlineRadioNo.value;
		}
		else if (e.target.inlineRadioOnCondition.checked) {
			data.realized = e.target.inlineRadioOnCondition.value;
		}
		else {
			data.realized = "No";
		}

		$("input:checkbox.projectName[type=checkbox]:checked").each(function () {
			projectsIds.push($(this).val());
		});
		data.projectsIds = projectsIds;

		$("input:checkbox.minutesName[type=checkbox]:checked").each(function () {
			minutesIds.push($(this).val());
		});
		data.minutesIds = minutesIds;

		$("input:checkbox.actionee[type=checkbox]:checked").each(function () {
			actioneeIds.push($(this).val());
		});
		data.actioneeIds = actioneeIds;

		data.additionalText = e.target.additionalText.value;
		data.actionee = e.target.actionee.value;
		data.percentageDone = e.target.percentageDone.value;

		data.taskContentNoHTML = $("<div/>").html(e.target.taskContent.value).text();
		data.additionalTextNoHTML = $("<div/>").html(e.target.additionalText.value).text();
		data.tagsNoHTML = $("<div/>").html(e.target.tags.value).text();
		data.otherProjectsNoHTML = $("<div/>").html(e.target.otherProjects.value).text();


	  Meteor.call('updateTask', data, function (error) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success(TAPi18n.__('The task has been updated successfully'));
				}
				if (typeof Router.current().params.filterBy !== 'undefined') {
					Router.go("tasks", {
						filterBy: Router.current().params.filterBy,
						filterVal: Router.current().params.filterVal,
					});
				}
				else {
					Router.go("tasks");
				}
			}
		});
		return false;
	},
	"click a.delete": function (e) {
    e.preventDefault();
    RKCore.log(this);
    Meteor.call('deleteTask', this._id, function () {});
		return false;
	},
});
