Template.editTasksTable.rendered = function () {
  var columns = [
  	{data: 'scheduledEndDate'},
  	{data: 'taskContent'},
  	{data: 'actionee'},
  	{data: 'includedInMinutes'},
  ];

  var colHeadersText = [];

  var colHeaders = [];
  var arrayLength = columns.length;
  var i;

  var demoData = [
      {scheduledEndDate: "24.12.2015", taskContent: "Do this", actionee: "DOT, PIN", includedInMinutes: "PV12"},
  		{scheduledEndDate: "22.12.2015", taskContent: "Do also this", actionee: "DOT", includedInMinutes: "PV12"},
  ];

  var $container = $("#myHandsonTableDiv");

  colHeadersText.scheduledEndDate = 'scheduledEndDate';
  colHeadersText.taskContent = 'taskContent';
  colHeadersText.actionee = 'actionee';
  colHeadersText.includedInMinutes = 'includedInMinutes';

  for (i = 0; i < arrayLength; i++) {
    colHeaders.push(colHeadersText[columns[i].data]);
  }

  $container.handsontable({
    data: demoData,
    colHeaders: colHeaders,
    columns: columns,
    rowHeaders: false,
    minSpareRows: 1,
    contextMenu: true,
  });
};

Template.editTasksTable.helpers({

});

Template.editTasksTable.events({
	'click a.save': function (e){
			var hotInstance = $("#myHandsonTableDiv").handsontable('getInstance');
			var data = hotInstance.getData();
      e.preventDefault();
			RKCore.log(data)
	    Meteor.call('saveTasksTable', data, function (error, result) {
  		  if (error) {
  		    // handle error
  		  }
        if (result) {
          if (typeof(toastr) !== 'undefined') {
        		toastr.success('The tasks have been imported successfully');
          }
  		  }
		  });
	},
});
