Template.dashboardRKTasks.rendered = function () {
	Meteor.call('getMyTasksToDo', function (error, result) {
		if (!error) {
			Session.set("dashboardRKTasks", result);
			return false;
		}
	});
};

Template.dashboardRKTasks.helpers({
	Tasks: function () {
		var tasks = Session.get("dashboardRKTasks");
		return tasks;
	},
	settingsTasks: function () {
    return {
			rowsPerPage: 100,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			rowClass: function (item) {
				var today = moment();
				var scheduledEndDate = moment(item.scheduledEndDate, "DD.MM.YYYY");
				//RKCore.log(today.diff(scheduledEndDate, 'days'));
				if ( today.diff(scheduledEndDate, 'days') === 0) {
					return 'warning';
				}
				if (today.diff(scheduledEndDate, 'days') > 0) {
					return 'danger';
				}
				return '';
			},
			fields: [
				{
					key: 'addedDate',
					label: TAPi18n.__("Added date"),
          hidden: true,
				},
				{
					key: 'addedBy',
					label: TAPi18n.__("Author"),
					fn: function (value) {
						var nickname = Members.collection.findOne({accountId: value}).profile.nickname;
						var nicknameHTML = "<span class='label label-info'>" + nickname + "</span>";
						return new Spacebars.SafeString(nicknameHTML);
					},
				},
				{
					key: 'formattedMinutesNames',
					label: TAPi18n.__("Minutes"),
					fn: function (value) {
						var i;
						var val = '';
						var n;
						if (value) {
							n = value.length;
							for (i = 0; i < n; i++) {
								val = val + "<span class='label label-info'>" + value[i] + "</span> ";
							}
							return new Spacebars.SafeString(val.trim());
						}
						return '';
					},
				},
				{
					key: 'scheduledStartDate',
					label: TAPi18n.__("Scheduled start date"),
				},
				{
					key: 'effectiveStartDate',
					label: TAPi18n.__("Effective start date"),
          hidden: true,
				},
				{
					key: 'taskContent',
					label: TAPi18n.__("Content"),
					fn: function (value) {
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'formattedProjectNames',
					label: TAPi18n.__("Projects"),
					fn: function (value, object) {
						var val = '';
						var i;
						var nProjects = value.length;
						for (i = 0; i < nProjects; i++) {
							val = val + "<span class='label label-info'>" + value[i] + "</span> ";
						}

						if (object.otherProjects) {
							val2 = object.otherProjects.replace(/;/g, ',').split(',');
			        htmlTag = val2.map(function (tag) {
			          return "<span class='label label-info'>" + tag + "</span>";
			        });
			        val = val + htmlTag.join(' ');
						}

						return new Spacebars.SafeString(val.trim());
					},
				},
				{
					key: 'tags',
					label: TAPi18n.__("Tags"),
					hidden: true,
					fn: function (value) {
						var val;
						if (value) {
							val = value.replace(/;/g, ',').split(',');
			        htmlTag = val.map(function (tag) {
			          return "<span class='label label-info'>" + tag + "</span>";
			        });
			        return new Spacebars.SafeString(htmlTag.join(' '));
						}
						return "";
					},
				},
				{
					key: 'formattedActioneeNames',
					label: TAPi18n.__("Actionee"),
					fn: function (value, object) {
            var i;
            var val = '';
            var n;
            if (value) {
							var nActionee = value.length;
							for (i = 0; i < nActionee; i++) {
								val = val + "<span class='label label-info'>" + value[i] + "</span> ";
							}
            }

						if (object.actionee) {
							val2 = object.actionee.replace(/;/g, ',');
			        actionee = val2.split(',');
              n = actionee.length;
              for (i = 0; i < n; i++) {
                actioneeId = actionee[i];
                val = val + "<span class='label label-info'>" + actionee[i] + "</span> ";
                }
              val = val.trim();
              }
						return new Spacebars.SafeString(val);
					},
				},
				{
					key: 'scheduledEndDate',
					label: TAPi18n.__("Scheduled end date"),
				},
				{
					key: 'effectiveEndDate',
					label: TAPi18n.__("Effective end date"),
          hidden: true,
				},
				{
					key: 'realized',
					label: TAPi18n.__("Realized"),
					hidden: true,
					fn: function (value) {
						var val;
						if (value === "Yes") {
							val = TAPi18n.__("Yes");
						}
						else if (value === "No") {
							val = TAPi18n.__("No");
						}
						else {
							val = TAPi18n.__("On condition");
						}
						return val;
					},
				},
				{
					key: 'percentageDone',
					label: TAPi18n.__("Percentage Done"),
          hidden: true,
					fn: function (value, object) {
						var val = value;
						if (object.realized === "Yes") {
							val = 100;
						}
						progressBar = val + "%";
						return new Spacebars.SafeString(progressBar);
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var additionalTextLink = '';
						var editLink;
						var markAsDoneLink = ' <a href="#" title="' + TAPi18n.__('Mark as done') + '" class="markAsDone" '
							+ 'data-taskid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-ok" aria-hidden="true"></span>' +
							'</a> ';
						if (object.additionalText) {
              additionalTextLink = '<a href="#"'
  							+ ' class="showTaskAdditionalText" title="' + TAPi18n.__('Show follow-up') + '"'
  							+ ' data-taskid="' +	object._id + '"'
								+ ' data-taskcontent="' +	object.taskContent + '"'
								+ ' data-taskadditionaltext="' +	object.additionalText + '"'
  							+ '>[...]</a> ';
						}
						editLink = '<a href="' +
							Router.routes.editTask.path({_id: object._id, filterBy: Router.current().params.filterBy, filterVal: Router.current().params.filterVal})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							//+ ' data-wheretocomeback="' +	Router.current().params + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(markAsDoneLink + additionalTextLink + editLink);
					},
				},
			],
		};
	},
});

Template.dashboardRKTasks.events({
	"click a.markAsDone": function (e) {
	    e.preventDefault();
	    Meteor.call('markAsDone', e.currentTarget.dataset.taskid, function () {
				Meteor.call('getMyTasksToDo', function (error, result) {
					if (!error) {
						Session.set("dashboardRKTasks", result);
						return false;
					}
				});
			});
			return false;
	},
	"click a.showTaskAdditionalText": function (e) {
		var taskContent = e.currentTarget.dataset.taskcontent;
		var taskAdditionalText = e.currentTarget.dataset.taskadditionaltext;
	  e.preventDefault();
    bootbox.dialog({
      title: taskContent,
      message: taskAdditionalText,
    });
		return false;
	},
});
