var progress = new ReactiveVar('');

getTasks = function () {
  var showRealizedTasks = Session.get("RKTasks-showRealizedTasks");
  var realizedArray;
  if (showRealizedTasks) {
    realizedArray = ["Yes", "No", "onCondition"];
  }
  else {
    realizedArray = ["No", "onCondition"];
  }
  return Tasks.find(
    {
      realized: { $in: realizedArray },
    }
    ).fetch();
};

getTruncatedTaskAdditionalText = function (url, txt) {
  var nWords = 0;
  var truncatedArray = txt.split(" ");
  var visiblePart;
  var tooltipPart;
  check(url, Match.OneOf(null, String));
  check(txt, String);
  if (url) {
    if (url.length > 0) {
      RKCore.log("url : " + url);
    }
  }

	if ((txt === "") || (typeof txt === 'undefined')) {
		return "-";
	}

	if (truncatedArray.length > nWords) {
		visiblePart = truncatedArray.splice(0, nWords).join(" ");
		tooltipPart = truncatedArray.join(' ');
		return new Spacebars.SafeString(visiblePart + " <a href='" + url + "' rel='tooltip' data-html='true' title='" + tooltipPart + "'>[...]</a>");
	}
};

updateGantt = function () {
	var allTasks = [];
	var nTasks;
	var duration;
	var i;
	var data = [];
	var tasksGantt = {};
	var showRealizedTasks = Session.get("RKTasks-showRealizedTasks");
	var realizedArray;
	if (showRealizedTasks) {
		realizedArray = ["Yes", "No", "onCondition"];
	}
	else {
		realizedArray = ["No", "onCondition"];
	}
	allTasks = Tasks.find(
		{
			$and: [
				{
					realized: { $in: realizedArray },
				},
				{
					showInGantt: true,
				},
			],
		}
	).fetch();

	nTasks = allTasks.length;

	gantt.config.work_time = true; //removes non-working time from calculations
	gantt.skip_off_time = true;    //hides non-working time in the chart
	gantt.config.readonly = true;

	for (i = 0; i < nTasks; i++) {
		RKCore.log("TaskContent : " + allTasks[i].taskContent);
		duration = gantt.calculateDuration(moment(allTasks[i].scheduledStartDate, "DD.MM.YYYY"), moment(allTasks[i].scheduledEndDate, "DD.MM.YYYY"));
		RKCore.log("Duration in working day : " + duration);
		data.push({
			id: allTasks[i]._id,
			text: allTasks[i].taskContent,
			start_date: moment(allTasks[i].scheduledStartDate, "DD.MM.YYYY").format("DD-MM-YYYY"),
			duration: duration,
			order: "10",
			progress: allTasks[i].percentageDone,
			open: false,
		});
	}
	tasksGantt.data = data;

	gantt.templates.scale_cell_class = function (date) {
	    if (!gantt.isWorkTime(date)) {
	        return "weekend";
	    }
	};
	gantt.templates.task_cell_class = function (item, date) {
	    if (!gantt.isWorkTime(date)) {
	        return "weekend";
	    }
	};
	gantt.templates.rightside_text = function (start, end, task) {
		if (task.type === gantt.config.types.milestone) {
			return task.text;
		}
		return "";
	};
	gantt.config.columns = [
	    {
				name: "text",
				label: "Task name",
				width: "*",
				tree: true,
			},
	    {
				name: "start_time",
				label: "Start time",
				template: function (obj) {
					return gantt.templates.date_grid(obj.start_date);
				},
				align: "center",
				width: 60,
			},
	    {
				name: "duration",
				label: "Duration",
				align: "center",
				width: 60,
			},
	];

	gantt.config.grid_width = 390;
	gantt.config.date_grid = "%F %d";

	gantt.config.scale_unit = "day";
	gantt.config.date_scale = "%d.%m";

	gantt.config.scale_height  = 60;
	gantt.config.subscales = [
		{ unit: "week",
			step: 1,
			date: "Week #%W",
		},
	];
	gantt.config.order_branch = true;
	//gantt.config.sort = true;
	//gantt.refreshData();
	gantt.init("tasksGantt");
	gantt.parse(tasksGantt);
	return tasksGantt;
};

Template.tasks.rendered = function () {
	Session.setDefault("RKTasks-showRealizedTasks", false);
};

