Template.viewProjects.rendered = function () {

};

Template.viewProjects.helpers({
	currentUsername: function () {
		var currentUserId = Meteor.userId();
		var currentUser = Members.collection.findOne({accountId: currentUserId});
		var currentUsername = currentUser.profile.nickname;
		return currentUsername;
	},
	projectManagerUsername: function (projectManagerAccountId) {
		var currentUser = Members.collection.findOne({accountId: projectManagerAccountId});
		var projectManagerUsername = currentUser.profile.nickname;
		return projectManagerUsername;
	},
	Projects: function () {
    return Projects.find().fetch();
	},
	settingsProjects: function () {
    return {
			rowsPerPage: 10,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			fields: [
				{
					key: 'projectNumber',
					label: TAPi18n.__("ID"),
				},
				{
					key: 'projectName',
					label: TAPi18n.__("Project name"),
				},
				{
					key: 'projectDescription',
					label: TAPi18n.__("Project description"),
				},
				{
					key: 'projectManagerIds',
					label: TAPi18n.__("Project manager"),
					fn: function (value) {
						var i;
            var val = '';
            var n;
            if (value) {
              n = value.length;
              for (i = 0; i < n; i++) {
                member = Members.collection.findOne(value[i]);
                if (typeof(member) !== 'undefined') {
                  val = val + "<span class='label label-info'>" + member.profile.nickname + "</span> ";
                }
              }
            }
						return new Spacebars.SafeString(val);
					},
				},
				{
					key: 'tags',
					label: TAPi18n.__("Tags"),
					fn: function (value) {
						var val = value.replace(/;/g, ',');
		        tag = val.split(',');
		        htmlTag = tag.map(function (tag) {
		          return "<span class='label label-info'>" + tag + "</span>";
		        });
		        return new Spacebars.SafeString(htmlTag.join(' '));
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var createTaskLink = '<a href="#"'
							+ ' class="createTask" title="' + TAPi18n.__('Add a task to this project') + '"'
							+ ' data-projectid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-plus" aria-hidden="true"></span>' +
							'</a> ';
						var createMinutesLink = '<a href="#"'
							+ ' class="createMinutes" title="' + TAPi18n.__('Add minutes to this project') + '"'
							+ ' data-projectid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-file" aria-hidden="true"></span>' +
							'</a> ';
						var editLink = '<a href="' +
							Router.routes.editProject.path({_id: object._id})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						var seeAllPVLink = '<a href="' +
							Router.routes.minutes.path({filterBy: "project", filterVal: object._id})
							+ '" title="' + TAPi18n.__('See all minutes for this project') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-book" aria-hidden="true"></span>' +
							'</a> ';
						var duplicateLink = '<a href="#"'
							+ ' class="duplicateProject title="' + TAPi18n.__('Duplicate') + '"'
							+ ' data-projectid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-duplicate" aria-hidden="true"></span>' +
							'</a> ';
						var deleteLink = ' <a href="#" title="' + TAPi18n.__('Delete') + '" class="deleteProject" '
							+ 'data-projectid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-trash" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(createTaskLink + createMinutesLink + editLink + seeAllPVLink + duplicateLink + deleteLink);
					},
				},
			],
		};
	},
});

Template.viewProjects.events({
	"click a.createProject": function (e) {
	    e.preventDefault();
	    Meteor.call('createProject', function (error, result) {
				if (!error) {
					projectId = result;
					Router.go("editProject", {_id: projectId});
				}
			});
			return false;
	},
	"click a.deleteProject": function (e) {
	    e.preventDefault();
			bootbox.confirm(TAPi18n.__("Are you sure you want to delete this project ?"), function (result) {
 			 if (result) {
 				 Meteor.call('deleteProject', e.currentTarget.dataset.projectid);
 			 }
 		 	});
			return false;
	},
	"click a.duplicateProject": function (e) {
	    e.preventDefault();
	    Meteor.call('duplicateProject', e.currentTarget.dataset.projectid, function (error, result) {
				if (!error) {
					newId = result;
					Router.go("editProject", {_id: newId});
				}
			});
			return false;
	},
	"click a.createTask": function (e) {
			var data = {};
			e.preventDefault();
			data.projectsIds = e.currentTarget.dataset.projectid;
	    Meteor.call('createTask', data, function (error, result) {
				if (!error) {
					taskId = result;
					Router.go("editTask", {_id: taskId});
				}
			});
			return false;
	},
	"click a.createMinutes": function (e) {
			var data = {};
			e.preventDefault();
			data.projectsIds = e.currentTarget.dataset.projectid;
			Meteor.call('createMinutes', data, function (error, result) {
				if (!error) {
					minutesId = result;
					Router.go("editMinutes", {_id: minutesId});
				}
			});
			return false;
	},
});
